import { Col, Drawer, Form, Row } from "antd";
import moment from "moment";
import { useCallback, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Swal from "sweetalert2";
import { Fn } from "../../../../helper/function";
import { TaskSprintManageService } from "../../../../services/taskService";
import { MyFormItem } from "../../../core/Input";
import { MyButton } from "../../../core/MyButton";
import { ProjectStatusComponent } from "../ProjectStatusComponent";

export default function ModifyComponent(props: any) {
    const [name, setName] = useState('');
    const [dateFrom, setDateFrom] = useState(moment());
    const [dateTo, setDateTo] = useState(moment());
    const [status, setStatus] = useState();
    const { projectKey, keyData, setKeyData, isLoading, setIsLoading, visible, setVisible, loadData } = props;

    let loadDataDetail = useCallback(() => {
        if (keyData !== undefined) {
            setIsLoading(true);
            TaskSprintManageService.GetDetail(projectKey, keyData)
                .then((rs: any) => {
                    if (rs.isSuccess) {
                        let row = rs.data;
                        setName(row.name);
                        setDateFrom(moment(row.dateFrom.timeUTC));
                        setDateTo(moment(row.dateTo.timeUTC));
                        setStatus(row.status.id);
                    }
                })
                .finally(() => setIsLoading(false))
        }
    }, [projectKey, keyData, setIsLoading])

    useEffect(() => {
        loadDataDetail();
    }, [loadDataDetail]);

    function HandleOnClose() {
        setVisible(false);
        setName('');
        setDateFrom(moment());
        setDateTo(moment());

        if (setKeyData !== undefined) {
            setKeyData(undefined);
        }
    }

    function HandleSubmitForm() {
        let body = {
            name: name,
            dateFrom: dateFrom.format("yyyy-MM-DD"),
            dateTo: dateTo.format("yyyy-MM-DD"),
            status: status
        };

        setIsLoading(true);

        if (keyData !== undefined) {
            Edit(body);
        } else {
            Add(body);
        }
    }

    function Add(body: any) {
        TaskSprintManageService.Add(projectKey, body)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    Fn.PrintMsgSuccess(rs);
                    HandleOnClose();
                    loadData();
                }
            })
            .finally(() => setIsLoading(false));
    }

    function Edit(body: any) {
        TaskSprintManageService.GetDetail(projectKey, keyData)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    body = Fn.GetTimestamp(rs, body);
                    TaskSprintManageService.Edit(projectKey, keyData, body)
                        .then((rs: any) => {
                            if (rs.isSuccess) {
                                Fn.PrintMsgSuccess(rs);
                                HandleOnClose();
                                loadData();
                            }
                        })
                        .finally(() => setIsLoading(false))
                } else setIsLoading(false);
            })
    }

    function handleDeleteSprint() {
        Swal.fire({
            title: "Xác nhận xóa",
            text: "Bạn có muốn xóa sprint này không?",
            showDenyButton: true,
            confirmButtonText: "Có",
            denyButtonText: "Không",
        }).then((result: any) => {
            if (result.isConfirmed) {
                doDeleteSprint()
            }
        })
    }

    function doDeleteSprint() {
        setIsLoading(true);
        TaskSprintManageService.GetDetail(projectKey, keyData)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    TaskSprintManageService.Delete(projectKey, keyData, Fn.GetTimestamp(rs))
                        .then((rs: any) => {
                            if (rs.isSuccess) {
                                Fn.PrintMsgSuccess(rs);
                                HandleOnClose();
                                loadData();
                            }
                        })
                        .finally(() => setIsLoading(false))
                } else setIsLoading(false);
            })
    }

    return (
        <Drawer
            title="Chi tiết"
            open={visible}
            onClose={HandleOnClose}>

            <Form layout="vertical" onSubmitCapture={HandleSubmitForm}>
                <MyFormItem.Input
                    label="Tên giai đoạn"
                    value={name}
                    setValue={setName} />

                <MyFormItem.DatePicker
                    label="Ngày bắt đầu"
                    value={dateFrom}
                    setValue={setDateFrom} />

                <MyFormItem.DatePicker
                    label="Ngày kết thúc"
                    value={dateTo}
                    setValue={setDateTo} />

                <ProjectStatusComponent
                    label="Trạng thái"
                    projectKey={projectKey}
                    type={2}
                    value={status}
                    setValue={setStatus} />

                <Row gutter={24}>
                    <Col span="12">
                        <MyButton
                            isLoading={isLoading}
                            htmlType="submit"
                            type="success"
                            value={keyData !== undefined ? "Chỉnh sửa" : "Thêm mới"} />
                    </Col>
                    <Col>
                        <MyButton
                            isLoading={isLoading}
                            type="danger"
                            disabled={keyData === undefined}
                            onClick={handleDeleteSprint}
                            value="Xóa sprint" />
                    </Col>
                </Row>

                <Link to={`/tools/tasks/projects/${projectKey}/sprint/${keyData}/toDoList`}>
                    Quản lý việc cần làm
                </Link>
            </Form>
        </Drawer>
    );
}